<template>
  <div class="teacher">
    <h3> {{item.name}}</h3>



    <el-form :inline="true" class="demo-form-inline" :loading="loading">
      <el-form-item label="姓名">
        <el-input v-model="search" placeholder="输入姓名查询" size="small"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="fetch" size="small">筛选老师</el-button>
      </el-form-item>



    </el-form>


      <el-table
        :data="list"
        style="width: 100%"
        border
        size="mini"
        v-loading="loading"
        @row-click="row_click"
      >

        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="name" label="名字"></el-table-column>
        <el-table-column prop="phone" label="电话"></el-table-column>
      </el-table>








    <br>
    <el-pagination

      layout="prev, pager, next"
      :total="total"
      @current-change="(p)=>current_page=p"
      :current-page="current_page"
      :page-size="page_size"
    >
    </el-pagination>
  </div>
</template>

<script>

  import ElButtonGroup from "../../node_modules/element-ui/packages/button/src/button-group.vue";
  import request from './config'
  import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";
  import ElForm from "../../node_modules/element-ui/packages/form/src/form.vue";

export default {
  components: {
    ElForm,
    ElButton,
    ElButtonGroup},
  name: 'shui2',
  data () {
    return {
      school_id:window.school_id,
      list:[],
      total:0,
      search:'',
      page_size:10,
      current_page:1,
      loading:false,

      item:{

      }
    }
  },

  watch:{
      current_page(){
          this.fetch()
      }
  },

  methods:{
      change_page(p){
          console.log(p)
      },

      async fetch(){

          var _limit = this.page_size,
              _page = this.current_page,
              search = this.search,
              that = this

          this.loading = true
          console.log(window.school,'window.school')
          var response = await request.get('/api/teacher/?school_id='+that.$route.params.id)
          this.list = response.data.objects

          this.loading = false
      },

      row_click(row,e,colnum){
        console.log(row,e,colnum)
        var that =this
        this.$router.push('/shui3/'+row.id)
      },

      async fetch_school(){
        var that= this


        var response = await request.get('/api/school/'+that.$route.params.id+'/')
        this.item = response.data
      }
  },

  mounted(){

      this.fetch()
      this.fetch_school()
  }
}
</script>

