<template>
  <div class="teacher">


    <h3> {{item.name}} 在 {{date[0]}}到{{date[1]}}期间的总计：</h3>

    <el-form :inline="true" class="demo-form-inline" :loading="loading">

      <el-form-item>
        <el-form-item label="选择时间">
          <el-date-picker
            v-model="date"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
      </el-form-item>


    </el-form>


      <el-table
        :data="list"
        style="width: 100%"
        border
        size="mini"
        v-loading="loading"
        @row-click="row_click"
      >

        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="exam_time" label="考试时间"></el-table-column>
        <el-table-column prop="exam_desc" label="考试描述"></el-table-column>
        <el-table-column prop="teacher_in_school_name" label="所在考点"></el-table-column>
        <el-table-column prop="exam_total" label="本次考试总计监考费" ></el-table-column>
        <el-table-column prop="school_exam_total"   label="所在考点总监考费" ></el-table-column>
        <el-table-column prop="teacher_total"   label="本次考试个人所得监考费" ></el-table-column>
      </el-table>








    <br>
    <el-pagination

      layout="prev, pager, next"
      :total="total"
      @current-change="(p)=>current_page=p"
      :current-page="current_page"
      :page-size="page_size"
    >
    </el-pagination>
  </div>
</template>

<script>

  import ElButtonGroup from "../../node_modules/element-ui/packages/button/src/button-group.vue";
  import request from './config'
  import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";
  import ElForm from "../../node_modules/element-ui/packages/form/src/form.vue";
  import moment from 'moment'
  import ElFormItem from "../../node_modules/element-ui/packages/form/src/form-item.vue";


export default {
  components: {
    ElFormItem,
    ElForm,
    ElButton,
    ElButtonGroup},
  name: 'shui3',
  data () {
    return {
      school_id:window.school_id,
      list:[],
      total:0,
      search:'',
      page_size:10,
      current_page:1,
      loading:false,
      date:[
        moment().subtract(1,'month').format('YYYY-MM-DD'),
        moment().format('YYYY-MM-DD'),
      ],
      item:{

      }
    }
  },

  watch:{
      current_page(){
          this.fetch()
      },
      date(){
        this.fetch()
      }
  },

  methods:{
      change_page(p){
          console.log(p)
      },

      async fetch(){

          var _limit = this.page_size,
              _page = this.current_page,
              search = this.search,
              that = this

          this.loading = true
          console.log(window.school,'window.school')
          var response = await request.get('/api/teacherexam/',{
            params:{
              teacher_id:that.$route.params.id,
              start:moment(that.date[0]).format('YYYY-MM-DD'),
              end:moment(that.date[1]).format('YYYY-MM-DD')
            }
          })
          this.list = response.data.objects

          this.loading = false
      },

      row_click(row,e,colnum){
        console.log(row,e,colnum)
        this.$router.push('/shui3/'+row.id)
      },

      async fetch_teacher(){
        var that= this
        var response = await request.get('/api/teacher/'+that.$route.params.id+'/')
        this.item = response.data
      }
  },

  mounted(){

      this.fetch()
      this.fetch_teacher()
  }
}
</script>

