<template>
  <div class="teacher">




    <el-form :inline="true" class="demo-form-inline" :loading="loading">
      <el-form-item>

      </el-form-item>

      <el-form-item label="姓名">
        <el-input v-model="search" placeholder="输入姓名查询" size="small"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button-group>
          <el-button type="primary" @click="fetch" size="small">查询</el-button>
          <el-button  @click="add_form.visible=true" size="small">增加考试办</el-button>

        </el-button-group>
      </el-form-item>

    </el-form>


    <el-table
      :data="list"
      style="width: 100%"
      border
      size="mini"
      v-loading="loading"
    >

      <el-table-column prop="id" label="id"></el-table-column>
      <el-table-column prop="office_name" label="办公室名字"></el-table-column>
      <el-table-column prop="exam_name" label="办公室负责考试"></el-table-column>
      <el-table-column prop="register_username" label="注册用户名"></el-table-column>


      <el-table-column  label="操作" width="135" >
        <template scope="scope">
          <el-button-group>
            <el-button type="danger" @click="remove(scope.row)" size="small">删除</el-button>
            <el-button @click="open_edit(scope.row)" size="small">编辑</el-button>

          </el-button-group>

        </template>
      </el-table-column>
    </el-table>


    <el-dialog title="增加办公室信息" :visible.sync="add_form.visible"
    >
      <el-form class="demo-form-inline" label-width="100px">
        <el-form-item label="办公室名字">
          <el-input v-model="add_form.office_name" placeholder="办公室名字"></el-input>
        </el-form-item>

        <el-form-item label="负责考试">
          <el-input v-model="add_form.exam_name" placeholder="负责考试"></el-input>
        </el-form-item>

        <el-form-item label="注册用户名">
          <el-input v-model="add_form.register_username" placeholder="输入用户名"></el-input>
        </el-form-item>

        <el-form-item label="注册密码">
          <el-input v-model="add_form.register_password" placeholder="输入密码"></el-input>
        </el-form-item>

      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="add_form.visible=false" size="small">取 消</el-button>
          <el-button type="primary" @click="add" size="small">确 定</el-button>
        </span>
    </el-dialog>


    <el-dialog title="编辑办公室信息" :visible.sync="edit_form.visible"
    >
      <el-form class="demo-form-inline"  label-width="100px">


        <el-form-item label="办公室名字">
          <el-input v-model="edit_form.office_name" placeholder="办公室名字"></el-input>
        </el-form-item>

        <el-form-item label="负责考试">
          <el-input v-model="edit_form.exam_name" placeholder="负责考试"></el-input>
        </el-form-item>

        <el-form-item label="注册用户名">
          <el-input v-model="edit_form.register_username" placeholder="输入用户名"></el-input>
        </el-form-item>

        <el-form-item label="是否修改密码">
          <el-switch
            v-model="edit_form.change_password"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>

        <el-form-item label="注册密码" v-if="edit_form.change_password">
          <el-input v-model="edit_form.register_password" placeholder="输入密码"></el-input>
        </el-form-item>


      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="edit_form.visible=false">取 消</el-button>
          <el-button type="primary" @click="edit">确 定</el-button>
        </span>
    </el-dialog>





    <br>
    <div style="display: inline-block;float: right">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="(p)=>page_num=p"
        :current-page="page_num"
        :page-size="page_size"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>

  import ElButtonGroup from "../../node_modules/element-ui/packages/button/src/button-group.vue";
  import request from './config'
  import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";
  import ElForm from "../../node_modules/element-ui/packages/form/src/form.vue";

  export default {
    components: {
      ElForm,
      ElButton,
      ElButtonGroup},
    name: 'office',
    data () {
      return {
        school_id:window.school_id,
        list:[],
        total:0,
        search:'',
        page_size:2,
        page_num:1,
        loading:false,
        add_form:{
          office_name:'',
          exam_name:'',
          register_username:"",
          register_password:'',
          visible:false,

        },
        edit_form:{
          id:'',
          office_name:'',
          exam_name:'',
          register_userid:'',
          register_username:'',
          register_password:'',
          visible:false,
          change_password:false
        },
        upload_dialog:{
          visible:false
        }
      }
    },

    watch:{
      page_num(){
        this.fetch()
      }
    },

    methods:{
      change_page(p){
        console.log(p)
      },
      open_edit(data){
        console.log(data)
        var change_password=this.edit_form.change_password
        data.visible = true
        this.edit_form = {
          visible:true,
          id:data.id,
          office_name:data.office_name,
          exam_name:data.exam_name,
          register_userid:data.register_userid,
          register_username:data.register_username,
          register_password:data.register_password,
          change_password:change_password
        }
      },
      async fetch(){

        var page_size = this.page_size,
          page_num = this.page_num,
          search = this.search,
          that = this

        this.loading = true

        var response = await request.get('/api/office/',{
          params:{
            page_size,
            page_num,
            search
          }
        })
        this.list = response.data.objects
        var page  = response.data.page
        this.page_num = page.page_num
        this.page_size = page.page_size
        this.total = page.total
        this.loading = false
      },
      async add(){
        var add_form = this.add_form

        console.log(add_form,'----')


        var response = await request.post('/api/office/',add_form)

        this.add_form.visible = false
        this.fetch()

      },
      async remove(data){

        try {
          await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
          var response = await request.delete('/api/office/'+data.id+'/')

          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        }catch (e){
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        }

      },
      async edit(){
        var edit_form = this.edit_form
        console.log(edit_form)
        var id = edit_form.id



        this.$message({
          type: 'success',
          message: '修改信息成功!'
        });



        var response = await request.put('/api/office/'+id +'/',edit_form)
        this.edit_form.visible = false
        this.fetch()
      }
    },
    mounted(){

      this.fetch()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

</style>



