<template>
  <div class="teacher">




    <el-tabs v-model="active" type="card" @tab-click="handleClick">
      <el-tab-pane label="学校" name="school">学校</el-tab-pane>
      <el-tab-pane label="招办" name="office">招办</el-tab-pane>
      <el-tab-pane label="金税三期" name="center">金税三期</el-tab-pane>

    </el-tabs>


      <el-table
        :data="list"
        style="width: 100%"
        border
        size="mini"
        v-loading="loading"
        @row-click="row_click"
      >

        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="name" label="名字"></el-table-column>
        <el-table-column prop="register_username" label="注册用户名"></el-table-column>
      </el-table>








    <br>
    <el-pagination

      layout="prev, pager, next"
      :total="total"
      @current-change="(p)=>current_page=p"
      :current-page="current_page"
      :page-size="page_size"
    >
    </el-pagination>
  </div>
</template>

<script>

  import ElButtonGroup from "../../node_modules/element-ui/packages/button/src/button-group.vue";
  import request from './config'
  import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";
  import ElForm from "../../node_modules/element-ui/packages/form/src/form.vue";
  import ElFormItem from "../../node_modules/element-ui/packages/form/src/form-item.vue";
  import moment from 'moment'

export default {
  components: {
    ElFormItem,
    ElForm,
    ElButton,
    ElButtonGroup},
  name: 'shui1',
  data () {
    return {
      school_id:window.school_id,
      list:[],
      total:0,
      search:'',
      page_size:10,
      current_page:1,
      loading:false,
      active:'school'
    }
  },

  watch:{
      current_page(){
          this.fetch()
      }
  },

  methods:{
      change_page(p){
          console.log(p)
      },

      async fetch(){

          var _limit = this.page_size,
              _page = this.current_page,
              search = this.search,
              that = this

          this.loading = true
          console.log(window.school,'window.school')
          var response = await request.get('/api/school/')
          this.list = response.data.objects

          this.loading = false
      },

      row_click(row,e,colnum){
        console.log(row,e,colnum)

        this.$router.push('/shui2/'+row.id )
      }

  },
  mounted(){

      this.fetch()
  }
}
</script>

