import Vue from 'vue'
import Router from 'vue-router'

import office from '../components/office.vue'
import school from '../components/school.vue'
import shui1 from '../components/shui1.vue'
import shui2 from '../components/shui2.vue'
import shui3 from '../components/shui3.vue'
import excel from '../components/excel.vue'
import data from '../components/data-export.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'office',
      component: office
    },
    {
      path: '/office',
      name: 'office',
      component: office
    },
    {
      path: '/school',
      name: 'school',
      component: school
    },
    {
      path: '/shui1',
      name: 'shui1',
      component: shui1
    },
    {
      path: '/shui2/:id',
      name: 'shui2',
      component: shui2
    },
    {
      path: '/shui3/:id',
      name: 'shui3',
      component: shui3
    },
    {
      path: '/excel/',
      name: 'excel',
      component: excel
    },
    {
      path: '/data/',
      name: 'data',
      component: data
    },
  ]
})



